const getRandomNumber = (min, max) => {
  return Math.floor(Math.random() * (max + 1 - min)) + min;
};

const getRandomNumberFloat = (min, max) => {
  const randomNumber = (Math.random() * (max + 1 - min) + min).toFixed(1);

  return randomNumber <= max ? randomNumber : getRandomNumberFloat(min, max);
};

export { getRandomNumber, getRandomNumberFloat };