import dayjs from 'dayjs';
import { getRandomNumber, getRandomNumberFloat } from './util';

const texts = [
  'Lorem ipsum dolor sit amet, consectetur adipiscing elit.',
  'Cras aliquet varius magna, non porta ligula feugiat eget.',
  'Fusce tristique felis at fermentum pharetra.',
  'Aliquam id orci ut lectus varius viverra.',
  'Nullam nunc ex, convallis sed finibus eget, sollicitudin eget ante.',
  'Phasellus eros mauris, condimentum sed nibh vitae, sodales efficitur ipsum.',
  'Sed blandit, eros vel aliquam faucibus, purus ex euismod diam, eu luctus nunc ante ut dui.',
  'Sed sed nisi sed augue convallis suscipit in sed felis.',
  'Aliquam erat volutpat. Nunc fermentum tortor ac porta dapibus.',
  'In rutrum ac purus sit amet tempus.'
];

const names = ['Ilya O\'Reilly', 'Matthew Coh', 'Kendy Wine', 'Josh Harris', 'Nerf Jamile'];

const generateEmotion = () => {
  const emotions = ['smile', 'sleeping', 'puke', 'angry'];

  return emotions[getRandomNumber(0, emotions.length - 1)];
};

const generateCommentsId = () => {
  const comments = [];

  for (let i = 0; i < getRandomNumber(0, 5); i++) {
    comments.push(getRandomNumber(1, 49));
  }

  return comments;
};

const generatePosterUrl = () => {
  const randomNumber = getRandomNumber(0, 6);

  return randomNumber === 0 || randomNumber === 1
    ? `./images/posters/${randomNumber}.png`
    : `./images/posters/${randomNumber}.jpg`;
};

const generateWritersAndActors = () => {
  const writers = [];

  for (let i = 0; i < 3; i++) {
    writers.push(names[getRandomNumber(0, names.length - 1)]);
  }

  return writers;
};

const generateReleaseDate = () => {
  return dayjs().subtract(getRandomNumber(0, 40), 'year')
    .subtract(getRandomNumber(0, 12), 'month')
    .subtract(getRandomNumber(0, 34), 'day')
    .format();
};

const generateCountry = () => {
  const countries = ['Finland', 'Russia', 'USA', 'China', 'Japan', 'Korea', 'France', 'Norway'];

  return countries[getRandomNumber(0, countries.length - 1)];
};

const generateGenre = () => {
  const genres = ['Comedy', 'Triller', 'Horror', 'Drama', 'Fantastic'];
  const result = [];

  for (let i = 0; i < getRandomNumber(1, 3); i++) {
    result.push(genres[getRandomNumber(0, genres.length - 1)]);
  }

  return result;
};

const generateDescription = () => {
  let description = '';

  for (let i = 0; i < getRandomNumber(1, 5); i++) {
    description += texts[getRandomNumber(0, texts.length - 1)];
  }

  return description;
};

const generateWatchingDate = () => {
  return dayjs().subtract(getRandomNumber(0, 3), 'year')
    .subtract(getRandomNumber(0, 12), 'month')
    .subtract(getRandomNumber(0, 34), 'day')
    .format();
};

const generateAlreadyWatched = () => Boolean(getRandomNumber(0, 1));

const generateComments = () => {
  const comments = [];

  for (let i = 1; i < 50; i++) {
    comments.push({
      id: i,
      author: names[getRandomNumber(0, names.length - 1)],
      comment: texts[getRandomNumber(0, texts.length - 1)],
      date: dayjs().format(),
      emotion: generateEmotion()
    });
  }

  return comments;
};

let idFilm = 0;

const generateFilm = () => {
  const alreadyWatched = generateAlreadyWatched();
  idFilm++;
  
  return {
    id: idFilm,
    comments: generateCommentsId(),
    filmInfo: {
      title: texts[getRandomNumber(0, texts.length - 1)],
      alternativeTitle: texts[getRandomNumber(0, texts.length - 1)],
      totalRating: getRandomNumberFloat(0, 10),
      poster: generatePosterUrl(),
      ageRating: getRandomNumber(0, 18),
      director: names[getRandomNumber(0, names.length - 1)],
      writers: generateWritersAndActors(),
      actors: generateWritersAndActors(),
      release: {
        date: generateReleaseDate(),
        releaseCountry: generateCountry()
      },
      runtime: getRandomNumber(40, 180),
      genre: generateGenre(),
      description: generateDescription()
    },
    userDetails: {
      watchlist: Boolean(getRandomNumber(0, 1)),
      alreadyWatched: alreadyWatched,
      watchingDate: alreadyWatched ? generateWatchingDate() : '',
      favorite: Boolean(getRandomNumber(0, 1))
    }
  };
};

export { generateComments, generateFilm };