import { render } from './framework/render.js';
import ProfileView from './view/profile-view.js';
import CountFilmsView from './view/count-films-view.js';
import NavigationView from './view/navigation-view.js';
import FilmsPresenter from './presenter/films-presenter.js';
import FilmsModel from './model/films-model.js';
import CommentsModel from './model/comments-model.js';

const header = document.querySelector('.header');
const footerStatistics = document.querySelector('.footer__statistics');
const main = document.querySelector('.main');
const filmsModel = new FilmsModel();
const commentsModel = new CommentsModel();
const filmsPresenter = new FilmsPresenter(main, filmsModel, commentsModel);

render(new ProfileView(), header);
render(new CountFilmsView(), footerStatistics);
render(new NavigationView(), main);

filmsPresenter.init();