import { render, remove } from '../framework/render.js';
import FilterView from '../view/filter-view.js';
import FilmsContainerView from '../view/films-container-view.js';
import FilmsListView from '../view/films-list-view.js';
import FilmCardView from '../view/film-card-view.js';
import ButtonMoreView from '../view/button-more-view.js';
import FilmsListTopView from '../view/films-list-top-view.js';
import FilmsListDiscussView from '../view/films-list-discuss-view.js';
import PopupView from '../view/popup-view.js';
import ClearListView from '../view/clear-list-view.js';

const FILMS_COUNTER = 5;

export default class FilmsPresenter {
  #container = null;
  #filmsModel = null;
  #filmsModelExtraTop = null;
  #filmsModelExtraDiscuss = null;
  #commentsModel = null;
  #filmsContainerComponent = new FilmsContainerView();
  #filmsListComponent = new FilmsListView();
  #filmsListTopComponent = new FilmsListTopView();
  #filmsListDiscussComponent = new FilmsListDiscussView();
  #buttonMoreComponent = new ButtonMoreView();
  #renderedFilmsCount = FILMS_COUNTER;

  constructor(container, filmsModel, commentsModel) {
    this.#container = container;
    this.#filmsModel = filmsModel.films;
    this.#filmsModelExtraTop = filmsModel.films;
    this.#filmsModelExtraDiscuss = filmsModel.films;
    this.#commentsModel = commentsModel.comments;
  }

  init = () => {
    this.#renderList();
  };

  #renderFilm = (filmData, container) => {
    const filmCardComponent = new FilmCardView(filmData);
    const popupComponent = new PopupView(filmData, this.#commentsModel);

    render(filmCardComponent, container);

    const openPopup = () => {
      document.body.append(popupComponent.element);
      document.body.classList.add('hide-overflow');
      document.addEventListener('keydown', onEscapeDown);
    }

    const closePopup = () => {
      document.removeEventListener('keydown', onEscapeDown);
      remove(popupComponent);
      document.body.classList.remove('hide-overflow');
    };

    const onEscapeDown = (evt) => {
      if (evt.key === 'Escape' || evt.key === 'Esc') {
        document.removeEventListener('keydown', onEscapeDown);
        closePopup();
      }
    };

    filmCardComponent.setOpenPopupHandler(openPopup);
    popupComponent.setClosePopupHandler(closePopup);
  };

  #loadMoreFilms = () => {
    this.#filmsModel
      .slice(this.#renderedFilmsCount, this.#renderedFilmsCount + FILMS_COUNTER)
      .forEach(film => this.#renderFilm(film, this.#filmsListComponent.element.querySelector('.films-list__container')));
    
    this.#renderedFilmsCount += FILMS_COUNTER;

    if (this.#renderedFilmsCount >= this.#filmsModel.length) {
      remove(this.#buttonMoreComponent);
    }
  };

  #renderList = () => {
    render(this.#filmsContainerComponent, this.#container);
    render(this.#filmsListComponent, this.#filmsContainerComponent.element);
    if (!this.#filmsModel.length) {
      this.#filmsListComponent.element.replaceChildren();
      this.#filmsListComponent.element.append(new ClearListView().element);
      return;
    }

    render(new FilterView(), this.#container);

    for (let i = 0; i < Math.min(this.#filmsModel.length, FILMS_COUNTER); i++) {
      this.#renderFilm(this.#filmsModel[i], this.#filmsListComponent.element.querySelector('.films-list__container'));
    }

    if (this.#filmsModel.length > FILMS_COUNTER) {
      render(this.#buttonMoreComponent, this.#filmsListComponent.element);
      this.#buttonMoreComponent.setClickHandler(this.#loadMoreFilms);
    }

    render(this.#filmsListTopComponent, this.#filmsContainerComponent.element);
    render(this.#filmsListDiscussComponent, this.#filmsContainerComponent.element);

    for (let i = 0; i < 2; i++) {
      this.#renderFilm(this.#filmsModelExtraTop[i], this.#filmsListTopComponent.element.querySelector('.films-list__container'));
      this.#renderFilm(this.#filmsModelExtraDiscuss[i], this.#filmsListDiscussComponent.element.querySelector('.films-list__container'));
    }
  };
}